import React from 'react';
import { useDispatch } from 'react-redux';

import {ReactComponent as Remove} from '../../assets/icons/delete-trash.svg'
import {ReactComponent as Plus} from '../../assets/icons/plus.svg'
import {ReactComponent as Minus} from '../../assets/icons/minus.svg'

import './cartproduct.css';

const CartProduct = ({ productInfo, actionClickInc, actionClickDec, deleteAction }) => {

  const dispatch = useDispatch();

  const stepperAction = (name, action) => {
    return (
      <button className='product-item__button' onClick={() => dispatch(action(productInfo.id))}>
          {
            name === 'minus'
              ? <Minus className='product-item__icon'/>
              : <Plus className='product-item__icon'/>
          }
      </button>
    )
  };

  return (
    <li className='product-item'>
      <div className='product-item__img'>
        <img src={productInfo.image} />
      </div>
      <div className='product-item__block'>
        <p className='product-item__price'>
          {productInfo.price} ₽ <span>x {productInfo.count}</span>
        </p>
        <p className='product-item__description'>
          {productInfo.description}
        </p>
        <div className='product-item__actions'>
          <div className='product-item__stepper'>
            {stepperAction('minus', actionClickInc)}
            <div className='product-item__count'>{productInfo.count}</div>
            {stepperAction('plus', actionClickDec)}
          </div>
          {console.log(productInfo.id, 'productInfo.id')}
          <div className='product-item__remove' onClick={() => dispatch(deleteAction(productInfo.id))}>
            <Remove className='product-item__icon product-item__icon--remove' />
          </div>
        </div>
      </div>
  </li>
  )
}

export default CartProduct;