import React from 'react';
import { Link, useLocation } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux';

import LayOut from '../../components/layout';
import {ReactComponent as FavoriteTab} from '../../assets/icons/favorite-like-love.svg';

import { addToFavoriteList } from "../../features/products/productsSlice";

import './products.css';


const Products = () => {
  const location = useLocation();
  const { products } = useSelector(state => state.products);
  const dispatch = useDispatch();

  const sliceText = (text) => text.slice(0, 35);

  const addToFavorite = (id) => {
    console.log(id)
    dispatch(addToFavoriteList(id))
    console.log('first')
  };

  return (
    <LayOut>
      <div className='products'>
        {console.log(products)}
        <h1 className='products-title'>Телефоны</h1>
        <div className='products-list'>
          {products.map((product, i) => (
            <div className='products-card' key={product.name + i}>
              <div className='products-favorite' onClick={() => addToFavorite(product.id)}>
                <FavoriteTab
                  className={`products-favorite__icon ${product.isFavorite ? 'products-favorite__icon-active' : ''}`}
                />
              </div>
              <Link to={`${location.pathname}${product.link}`} className='products-link' state={product}>
                <div className='products-block'>
                  <img className='products-block__img' src={product.image}/>
                </div>
                  <p className=' products-description'>{sliceText(product.description)}...</p>
                  <p  className='products-price'>{product.price} <span>₽</span></p>
              </Link>
            </div>
          ))}
        </div>
      </div>
    </LayOut>
  )
};

export default Products;