import { useEffect } from "react";
import { useRoutes } from "react-router-dom";
import { useTelegram } from "./hooks/useTelegram";

import Categories from './pages/categories';
import Products from './pages/products/index';
import Product from './pages/product/index';
import Cart from './pages/cart/index';
import Payment from './pages/payment/index';
import TabBar from './components/tabbar/index';


function App() {
  const { tg } = useTelegram();

  const homePath = 'fleabot/';

  const routes = useRoutes([
    { path: homePath, element:  <Categories/> },
    { path: homePath, children: [{ path: ':id', element:  <Products/> }] },
    { path: `${homePath}:id`, children: [{ path: ':id', element:  <Product/> }] },
    { path: `${homePath}cart`, element: <Cart/> },
    { path: `${homePath}payment`, element: <Payment/> },
  ]);

  useEffect(() => {
    tg.ready();
  }, [tg]);

  return (
    <>
      {routes}
      <TabBar/>
    </>
  );
}

export default App;
