import React, { useEffect } from 'react';
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux';

import {
  increment,
  decrement,
  calcTotalCost,
  deleteProduct,
  calcTotalCount
} from "../../features/cart/cartSlice";

import CartProduct from '../../components/cartproduct';
import LayOut from '../../components/layout';

import './cart.css';


const Cart = () => {
  const { cartProducts, totalCost, totalCount } = useSelector(state => state.cart);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(calcTotalCost());
    dispatch(calcTotalCount());
  }, [cartProducts]);

  return (
    <LayOut>
      {cartProducts.length > 0 ?
        <div className='cart'>
          <section className='cart-info'>
            <h2 className='cart-info__title'>Корзина ({totalCount})</h2>
            <div className='cart-info__total'>{totalCost} ₽</div>
          </section>
          <section className='cart-product'>
            <ul className='cart-product__list'>
              {cartProducts.map((product, idx) => (
                <CartProduct
                  productInfo={product}
                  actionClickInc={increment}
                  actionClickDec={decrement}
                  key={product.name + idx}
                  deleteAction={deleteProduct}
                />
              ))}
            </ul>
          </section>
          <section className='cart-order'>
            <Link to={'/fleabot/payment'} className='cart-order__create'>Оформить заказ</Link>
          </section>
        </div>
        :
          <div>
            <h2> Корзина пуста!</h2>
          </div>
        }
    </LayOut>
  )
};

export default Cart;
